import {
  Component,
  Input,
  Output,
  EventEmitter,
  AfterViewInit,
  ElementRef,
} from '@angular/core'
import { CommonModule } from '@angular/common'
import { IonButton } from '@ionic/angular/standalone'

@Component({
  selector: 'app-fill-button',
  templateUrl: './fill-button.component.html',
  styleUrls: ['./fill-button.component.scss'],
  imports: [CommonModule, IonButton],
})
export class FillButtonComponent implements AfterViewInit {
  @Input() disabled = false
  @Input() color = 'light'
  @Input() fill: 'clear' | 'outline' | 'solid' = 'outline'
  @Input() autoAnimate = true
  @Input() animationDuration = 2000 // 2 seconds

  @Output() buttonClick = new EventEmitter<void>()

  constructor(private elementRef: ElementRef) {}

  ngAfterViewInit() {
    if (this.autoAnimate) {
      this.waitForElementAndAnimate()
    }
  }

  private waitForElementAndAnimate() {
    // Simple delay to ensure the DOM is ready
    setTimeout(() => {
      this.startAnimation()
    }, 200)
  }

  onButtonClick() {
    this.buttonClick.emit()
  }

  private startAnimation() {
    // Find the ion-button inside the component
    const buttonElement = this.elementRef.nativeElement.querySelector(
      '.fill-button'
    ) as HTMLElement

    if (!buttonElement) {
      console.warn('FillButtonComponent: ion-button element not found in DOM')
      return
    }

    const duration = this.animationDuration
    const steps = 100
    const interval = duration / steps
    let currentStep = 0

    const animation = setInterval(() => {
      if (currentStep >= steps) {
        clearInterval(animation)
        return
      }

      const progress = (currentStep / steps) * 100
      buttonElement.style.setProperty('--fill-width', `${progress}%`)
      currentStep++
    }, interval)
  }

  resetAnimation() {
    const buttonElement = this.elementRef.nativeElement.querySelector(
      '.fill-button'
    ) as HTMLElement
    if (buttonElement) {
      buttonElement.style.setProperty('--fill-width', '0%')
    }
  }
}
